import React from 'react';
import Navi from './Navigation';
import { Card, Button } from 'react-bootstrap';
import picture from './img/pp.jpeg';
import picture1 from './img/bg.png';
import picture2 from './img/java.jpg';
import picture3 from './img/python.jpg';
import picture4 from './img/html.png';
import picture5 from './img/CSS.png';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Fade from 'react-bootstrap/Fade';
export default class Home extends React.Component {
  constructor(props) {

    super(props);

    this.state = {
      fadeIn:
        false
    };

    this.toggle = this.toggle.bind(this);

  }
  render() {
    return (
      <div >
        <Navi />
        <br />
        <h1 style={{ color: 'white', marginLeft: '4%' }}>ABOUT</h1>
        <br />
        <Container fluid={true} style={{padding: '40px'}}>
          <Row >
            <Col md={{ span: 6, offset: 0}}>
              <Row >
                <Card style={{ justifyContent: 'center', backgroundColor: "#FA8072",paddingRight:"10px" }}>
                  <br />
                  <Card.Img style={{ display: "block", marginLeft: 'auto', marginRight: 'auto', width: '50%' }} variant="top" src={picture} />
                  <Card.Body>
                    <Card.Title style={{ textAlign: 'center' }}><h2><strong>DRISTI BIYANI</strong></h2></Card.Title>
                    <Card.Text>
                      <h3>Hey I'm Dristi, a student of Manipal Institute of Technology studying Electrical and Electronics engineering. My goal is to keep learning something new everyday.</h3>
               
                    </Card.Text>
                  </Card.Body>
                </Card>
              </Row>
              <br/>
            </Col>
           
            <Col md={{ span: 6, offset: 0} } style={{padding:"30px"}} >
              <Row className>
                <Card style={{ backgroundColor: "#FA8072", width:'150%',padding:"10px"}}>
                  <br />
                  <p style={{ marginLeft: '20px' }}><h2><strong>SKILLS</strong></h2></p>
                  <Row>
                    <Col md={12} sm={6} lg={4}>
                      <ul>
                        <li><h3>JAVA</h3></li>
                      </ul>
                    </Col>
                    <Col md={12} sm={6} lg={4}>
                      <ul>
                        <li><h3>C</h3></li>
                      </ul>
                    </Col>
                    <Col md={12} sm={6} lg={4}>
                      <ul>
                        <li><h3>PYTHON</h3></li>
                      </ul>
                    </Col>
                  </Row>
                  <Row>
                    <Col md={12} sm={6} lg={4}>
                      <ul>
                        <li><h3>HTML5</h3></li>
                      </ul>
                    </Col>
                    <Col md={12} sm={6} lg={4}>
                      <ul>
                        <li><h3>CSS</h3></li>
                      </ul>
                    </Col>
                    <Col md={12} sm={6} lg={4}>
                      <ul>
                        <li><h3>JAVA-SCRIPT</h3></li>
                      </ul>
                      <br />
                    </Col>

                  </Row>
                </Card>
              </Row>
              <br />
           
              <Row >
                <Card style={{ backgroundColor: "#FA8072" }}>
                  <br />
                  <p style={{ marginLeft: '20px' }}><h2><strong>HOBBIES AND INTERESTS</strong></h2></p>
                  <Button variant="dark" style={{ width: '8rem', marginLeft: '20px' }} onClick={this.toggle}>Click me!</Button>
                  <Fade
                    in={this.state.fadeIn}
                    className='my-2'>
                    <Card.Text>
                      <br />
                      <p style={{ marginLeft: '20px' }}> <h3>Some of the activities that really make me happy are dancing and baking.</h3></p>
                      <br />
                      <p style={{ marginLeft: '20px' }}><h3>As of now I am particularly interested in developing my skills in graphic designing and web development.</h3></p>
                      
                    </Card.Text>
                  </Fade>
                </Card>
              </Row>
            </Col>
           <br/>
          </Row>
        </Container>
        <br />
      </div>

    );
  }

  toggle() {

    this.setState({

      fadeIn: !this.state.fadeIn

    });
  }
}