import React from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import './Footer.css';
function Footer() {

    return(
      <div>
            <footer style={{left:0,bottom:0,width: "100%",backgroundColor: "#343A40"}} >
            <Container fluid={true}>
                <Row>
                    <Col className="p-0" md={6} sm={6}>
                       <p style={{color:'white'}}>HAVE A NICE DAY 
                           </p> 
                    </Col>
                   <Col md={6} sm={6}>
                         <p style={{color:'white', textAlign:"right"}}>THANK YOU! </p>  
                      </Col>
                </Row>
            </Container>
        </footer>
        </div>
    );

}

export default Footer;

